<template><section><h1>MeasureTool</h1>
<ul>
<li>The <code v-pre>vc-measure-distance</code> component is used for distance measurement.</li>
<li>The <code v-pre>vc-measure-area</code> component is used for area measurement.</li>
<li>The <code v-pre>vc-measure-height</code> component is used for high measurement.</li>
</ul>
<h2>Example</h2>
<h3>Measuring distance, area, height</h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready" scene3DOnly>
        <vc-measure-distance :clampToGround="clampToGround" ref="measureDistance" @activeEvt="activeEvt" @measureEvt="measureEvt" @movingEvt="movingEvt" :removeLastPosition="removeLastPosition"></vc-measure-distance>
        <vc-measure-area ref="measureArea" @activeEvt="activeEvt" @measureEvt="measureEvt" @movingEvt="movingEvt" :clampToGround="clampToGround" :removeLastPosition="removeLastPosition"></vc-measure-area>
        <vc-measure-height ref="measureHeight" @activeEvt="activeEvt" @measureEvt="measureEvt" @movingEvt="movingEvt"></vc-measure-height>
        <vc-primitive-tileset :url="modelUrl" @readyPromise="readyPromise"></vc-primitive-tileset>
      </vc-viewer>
      <div class="demo-tool">
        <md-button class="md-raised md-accent" @click="toggle('measureDistance')">{{ distanceMeasuring ? 'stop' : 'distance' }}</md-button>
        <md-button class="md-raised md-accent" @click="toggle('measureArea')">{{ areaMeasuring ? 'stop' : 'area' }}</md-button>
        <md-button class="md-raised md-accent" @click="toggle('measureHeight')">{{ heightMeasuring ? 'stop' : 'height' }}</md-button>
        <md-button class="md-raised md-accent" @click="clear">clear</md-button>
        <span>clampToGround</span>
        <md-switch v-model="clampToGround"></md-switch>
        <span>removeLastPosition</span>
        <md-switch v-model="removeLastPosition"></md-switch>
      </div>
    </div>
  </template>
  
</doc-preview>
